import { Box, Drawer, IconButton, List, ListItem, ListItemButton, ListItemText, Typography } from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import FacebookIcon from '@mui/icons-material/Facebook';
import CloseIcon from '@mui/icons-material/Close';

import './NavBar.css';
import { useState } from 'react';

const pages = [
    { target: '/web', name: 'Accueil' },
    { target: '/web/#project', name: 'Le projet' },
    { target: '/web/#publications', name: 'Publications' },
    { target: '/web/#presse', name: 'Presse' },
    { target: '/web/#contact', name: 'Contact' },
];

export default function NavBar() {
    const [open, setOpen] = useState(false);

    const toggleDrawer = (newOpen: boolean) => () => {
        setOpen(newOpen);
    };

    const DrawerMenu = (
        <Box className="drawer-menu" role="presentation" onClick={toggleDrawer(false)}>
            <Box className='drawer-menu-content'>
                <div className="close-button">
                    <IconButton aria-label='close menu' onClick={toggleDrawer(false)}>
                        <CloseIcon />
                    </IconButton>
                </div>
                <List>
                    {pages.map((page) => (
                        <ListItem key={page.target} disablePadding>
                            <ListItemButton href={page.target}>
                                <ListItemText primary={page.name}/>
                            </ListItemButton>
                        </ListItem>
                    ))}
                </List>
            </Box>
        </Box>
    );

    return (
        <Box className="header">
            <div className="app-menu">
                <div className="socials-links">
                    <IconButton aria-label="facebook">
                        <FacebookIcon />
                    </IconButton>
                </div>
                <div className="menu-items">
                    <IconButton aria-label="menu"
                        onClick={toggleDrawer(true)}>
                        <MenuIcon />
                    </IconButton>
                    <Drawer open={open} onClose={toggleDrawer(false)}
                        anchor='right'>
                        {DrawerMenu}
                    </Drawer>
                </div>
            </div>

            <Box className="reason">
                <Typography>
                    Association de lutte contre le projet de carrière
                </Typography>
            </Box>
            <Box className="title">
                <Typography>Pas de carrière à Laà-Mondrans</Typography>
            </Box>
        </Box>
    )
}