import { Outlet } from "react-router-dom";
import NavBar from "../Components/NavBar/NavBar";
import FooterBar from "../Components/FooterBar/FooterBar";
import { Box } from "@mui/material";

export default function Home() {
    return (
        <Box className="App">

            <header>
                <NavBar />
            </header>

            <main>
                <Outlet />
            </main>

            <footer >
                <FooterBar />
            </footer>

        </Box>
    )
}