import { Box, CircularProgress, FilledInput, FormControl, FormHelperText, IconButton, InputAdornment, InputBase, InputLabel, OutlinedInput, Paper, TextField, } from "@mui/material";
import SendIcon from '@mui/icons-material/Send';
import CheckIcon from '@mui/icons-material/Check';

import './FooterBar.css';
import { useState } from "react";
import { ContactRepository } from "../../../UseCases/Contacts/ContactRepository";
import { APIRepository } from "../../../Repositories/DataSources/APIRepository";

export default function FooterBar() {
    const [success, setSuccess] = useState(false)
    const [loading, setLoading] = useState(false)
    const [dataInput, setDataInput] = useState({
        email: '', error: '', valid: true
    });

    const handleSubmit = (e: any, onSubmit: Function) => {
        e.preventDefault()
        if (success) {
            return
        }
        onSubmit()
    }

    const onChanged = (e: any) => {
        const { value } = e.target;
        setDataInput({ ...dataInput, email: value })
    }

    const validate = () => {
        let error = ''

        let valid = dataInput.valid;
        if (dataInput.email !== undefined) {
            valid = dataInput.email.match(/^([\w.%+-]+)@([\w-]+\.)+([\w]{2,})$/i) ? true : false
            error = 'Veuillez saisir une adresse email valide'
        }

        setDataInput({
            ...dataInput,
            valid: valid,
            error: error
        })
    }

    const onSubmit = () => {
        validate()

        if (dataInput.valid) {
            setLoading(true)
            const repository = new ContactRepository(new APIRepository())
            repository.subscribe(dataInput.email)
                .then((succeed) => {
                    setSuccess(succeed)
                    setLoading(false)
                })
                .catch((reason) => {
                    setLoading(false)
                    setSuccess(false)
                })
        }
    }

    return (
        <Box className="footer">

            <div className="newsletter">
                <h3>Recevoir des informations</h3>
                <form onSubmit={e => handleSubmit(e, onSubmit)}
                >
                    <FormControl variant="outlined" fullWidth required>
                        <InputLabel >Saisissez votre adresse email</InputLabel>
                        <FilledInput
                            value={dataInput?.email}
                            required
                            onChange={onChanged}
                            disabled={success || loading}
                            style={{ backgroundColor: '#fff' }}
                            type='text'
                            endAdornment={
                                <InputAdornment position="end">
                                    <IconButton aria-label="search" onClick={onSubmit} disabled={success}>
                                        {success ? <CheckIcon /> : <SendIcon />}
                                    </IconButton>
                                    {loading && (
                                        <CircularProgress size={35} sx={{ position: 'absolute' }} />
                                    )}
                                </InputAdornment>
                            }
                        />
                        <FormHelperText error hidden={dataInput.valid}>
                            {dataInput.error}
                        </FormHelperText>
                    </FormControl>
                </form>
            </div>
            <div className="legals-links">
                <div>Mentions légales</div>
                <div>Politique en matière de cookies</div>
                <div>Politique de confidentialité</div>
                <div>Conditions d’utilisation</div>
            </div>
            <div className="copyright">
                © Pas de carrière à Laà-Mondrans - 2024
            </div>
        </Box >
    );
}